.login{@apply h-screen bg-[#0e101c] pt-20 font-montserrat}.login form{max-width:500px;margin:0 auto;padding:0 15px}.login p{color:#bf1650;padding-top:5px}.login p::before{display:inline;content:"⚠ "}.login input{display:block;box-sizing:border-box;width:100%;color:#000;border-radius:4px;border:1px solid #fff;padding:10px 15px;margin:10px 0;font-size:14px}.login label{text-align:left;display:block;margin-top:20px;color:#fff;font-size:15px}.login button[type=submit],.login input[type=submit]{background:#ec5990;color:#fff;text-transform:uppercase;width:100%;border:none;margin-top:40px;padding:20px;font-size:16px;font-weight:100;letter-spacing:10px;cursor:pointer}.login button[type=submit]:hover,.login input[type=submit]:hover{background:#bf1650}.login button[type=submit]:active,.login input[type=button]:active,.login input[type=submit]:active{transition:.3s all;transform:translateY(3px);border:1px solid rgba(0,0,0,0);opacity:.8}.login button[type=submit],.login input:disabled{opacity:.4;cursor:not-allowed}.login input[type=button]:hover{transition:.3s all}.login button[type=submit],.login input[type=button],.login input[type=submit]{-webkit-appearance:none}