.loginButton{display:inline-block;background-color:#ff4656;border-radius:3px;color:#fff;text-align:center;font-size:20px;padding:5px;width:120px;transition:all .5s;cursor:pointer;margin:5px}.loginButton span{cursor:pointer;display:inline-block;position:relative;transition:.5s}.loginButton span:after{content:"»";position:absolute;opacity:0;top:0;right:-20px;transition:.5s}.loginButton:hover span{padding-right:20px}.loginButton:hover span:after{opacity:1;right:0}